// we dont need this import { Reducer,initialState } from './reducer';
import {createStore, combineReducers, applyMiddleware } from 'redux';
import thunk from 'redux-thunk';
import logger from 'redux-logger';
import { Dishes, DISHES } from './dishes';
import {Comments, COMMENTS } from './comments';
import {Promotions, PROMOTIONS} from './promotions';
import {Leaders, LEADERS} from './leaders';
import { createForms } from 'react-redux-form';
import { InitialFeedback } from './forms';

export const ConfigureStore = () => {
    const store = createStore(
        /*Reducer,
        initialState */ 
        combineReducers({
            
            dishes:Dishes,
            comments:Comments,
            promotions:Promotions,
            leaders:Leaders,
            ...createForms({
                feedback:InitialFeedback
            })

        }),applyMiddleware(thunk, logger)
        )

        return store;
        
}